import Vue from "vue";
import App from "./App.vue";
import VueRouter from "vue-router";
import "bootstrap/dist/css/bootstrap.css";
import "bootstrap";
import AWS from "aws-sdk";

import Profile from "./components/Profile.vue";
import Header from "./components/header/Header.vue";
import Project from "./components/Project.vue";
import Modal from "./components/Modal.vue";
import Contact from "./components/Contact.vue";
import ReferMe from "./components/ReferMe.vue";

Vue.config.productionTip = false;
// Make BootstrapVue available throughout your project
Vue.use(VueRouter);
Vue.use(AWS);

Vue.component("my-profile", Profile);
Vue.component("the-header", Header);
Vue.component("my-employeement", Project);
Vue.component("my-modal", Modal);
Vue.component("refer-me", ReferMe);

const routes = [
  {
    path: "/employement",
    component: Project,
  },
  {
    path: "/profile",
    component: Profile,
  },
  {
    path: "/contact",
    component: Contact,
  },
  {
    path: "/referme",
    component: ReferMe,
  },
  {
    path: "*",
    component: Profile,
  },
];

const router = new VueRouter({
  routes,
  mode: "history",
});

new Vue({
  router: router,
  render: (h) => h(App),
}).$mount("#app");
